import { createApp } from 'vue';
// axios
import axios from 'axios';
// VueAxios
import VueAxios from 'vue-axios';
// font-awesome
import { library } from '@fortawesome/fontawesome-svg-core';
// font-awesome
import { fas } from '@fortawesome/free-solid-svg-icons'; // font-awesome
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
// ÂºïÁî® element-plus
import ElementPlus from 'element-plus';
// element-plus-Ê®£Âºè
import 'element-plus/dist/index.css';
import * as icons from '@element-plus/icons-vue';
// element-ÁπÅÈ´î‰∏≠ÊñáÁâà.
import locale from 'element-plus/lib/locale/lang/zh-tw';
import _ from 'lodash';
import Loading from 'vue-loading-overlay';
// vue-alert2
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';
// google font
import 'material-icons/iconfont/material-icons.css';
// e-chart
import * as echarts from 'echarts'; // e-charts ÂúìÈ§ÖÂúñ
// ÊôÇÈñìÊ†ºÂºèËΩâÊèõ
import moment from 'moment';
// ÂºïÂÖ• I18n Â•ó‰ª∂
import i18n from './i18n/index';
// Ê∑±Ëâ≤Ê®°Âºè
import 'element-plus/theme-chalk/dark/css-vars.css';
// ËºâÂÖ•bootstrapÂÖ®ÈÉ®jsÂäüËÉΩ
import 'bootstrap/dist/js/bootstrap';
import App from './App.vue';
import router from './router';
import '@fortawesome/fontawesome-free/css/all.css';

const app = createApp(App);
// ÂÖ®Â±ÄË®ªÂÜäicons
Object.keys(icons).forEach((key) => {
  app.component(key, icons[key]);
});

// Vue3ÂÖ®Â±ÄË®ªÂÜä ÁÑ°Ê≥ïÂñÆÁ¥î‰ΩøÁî®app.useÊúÉÂ†±ÈåØ
app.config.globalProperties.library = library;
app.config.globalProperties.fas = fas;
app.config.globalProperties._ = _;
// ÂúìÈ§ÖÂúñ
app.config.globalProperties.$echarts = echarts;
app.config.globalProperties.FontAwesomeIcon = FontAwesomeIcon;
app.config.globalProperties.i18n = i18n;
app.config.globalProperties.$t = i18n.global.t;
app.config.globalProperties.$isLoading = false; // È†êË®≠ËΩâÂúàËÆÄÊ¢ùÈóúÈñâ
// ÂÖ®Â±Ämethods
app.config.globalProperties.$filters = {
  currencyUSD(value) {
    return `$${value}`;
    // <p>{{ $filters.currencyUSD(accountBalance) }}</p> ÂÖ®Â±ÄË®ªÂÜäÊñπÊ≥ï
  },
  dateTime(value) {
    return moment(value).format('YYYY-MM-DD');
  },
  // ËΩâÊèõÊàêÂÖ®Êï∏Â≠óÊ†ºÂºè
  dateTime2(value) {
    return moment(value).format('x');
  },
  // ËΩâÊèõÂπ¥ÊúàÊó•ÊôÇÂàÜÁßí
  dateTime3(value) {
    return moment(value).format('YYYY-MM-DD HH:mm:ss');
  },
  // ËΩâÊèõÊàêÊôÇÈñìÊó•Êúü+ÁßíÊï∏
  dateTime4(value) {
    return moment(value).format('YYYY-MM-DD');
  },
  // ËΩâÊèõÊàêÂÖ®Êï∏Â≠óÊ†ºÂºè
  dateTime5(value) {
    return moment(value).format('');
  },
  thisMonthDays(value) {
    return moment(value).daysInMonth();
  },
  thisDate(value) {
    return moment(value).format('DD');
  },
  thisMonth(value) {
    return moment(value).format('MM');
  },
  thisYYDD(value) {
    return moment(value).format('YYYY-MM');
  },
  getYear(value) {
    return moment(value).format('YYYY');
  },
};
app.component('Loading', Loading);
app.use(i18n);
app.use(_);
app.use(VueSweetalert2);
app.use(router);
app.use(VueAxios, axios);
app.use(ElementPlus, { locale });
app.mount('#app');
